.App-playlist {
    width: 55vw;
    margin: auto;
    margin-top: 5vw;
    margin-bottom: 5vw;
}


/** Now Playing **/

.now-playing__name {
    font-size: 0.7em;
    margin-bottom: 0.2em;
}

.now-playing__artist {
    margin-bottom: 1em;
}

.now-playing__status {
    margin-bottom: 1em;
}

.now-playing__img {
    float: left;
    height: 45px;
    width:45px;
}

.album-img {
    width: 100%;
}

.now-playing__side {
    margin-left: 5%;
    width: 45%;
}

/** Progress **/

.progress {
    border: 1px solid #eee;
    height: 6px;
    border-radius: 3px;
    overflow: hidden;
}

.progress__bar {
    background-color: #eee;
    height: 4px;
}

/** Intro **/

.Intro-text {
    margin-top: 5vw;
}

.Intro-pic {
    float: right;
    width: 55vw;
    margin-right: 5vw;
    margin-top: 5vw;
}

.Intro-list {
    float: left;
    margin: 0 auto;
    width: 30vw;
    margin-left: 5vw;
    margin-top: 5vw;
}

/** Landing **/

.Landing {
    background-color: #282c34;
    min-height: 90vh;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.Landing-leftpic {
    float: left;
    width: 30vw;
}

.Landing-gif {
    float: right;
    width: 15vw;
}